.container{
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 3rem 0;
    text-align: center;
}
.card-container{
    height: 350px;
    width: 300px;
    background: linear-gradient( rgba(255, 255, 255, 0.7) 10%, rgba(255, 255, 255, 0.1) 100%);
    border-radius: 30px;
    max-width: 600px;
    padding: 1rem;
} 

.bmi-form .btn{
       border-radius: 30px;
}


@media screen and (max-width: 1140px) {

    .container{
        height: 50vh;
        flex-direction: column;
        justify-content: space-around;
        margin-bottom: 50px;
    }


    .card-container{
        height: 320px;
        width: 270px;
        background: linear-gradient( rgba(255, 255, 255, 0.7) 10%, rgba(255, 255, 255, 0.1) 100%);
        border-radius: 20px;
        max-width: 500px;
        padding: .2rem;
    } 

}
    